@import url("../../styles.css");

* {
    font-family: 'Poppins', sans-serif;
}

.topIcon {
    font-size: 20px;
    margin-right: 10px;
    color: #444;
    cursor: pointer;
}

.top{
    width: 100%;
    height: 50px;
    background-color: var(--altback);   
    position: sticky;
    top: 0;
    display: flex;
    align-items: center;
    font-family: 'Poppins', sans-serif;
    z-index: 999;
    color: var(--teal);
    box-shadow: 3px 3px 7px 2px var(--shadow);
}

.topLeft, .topRight {
    flex: 3;
    display: flex;
    align-items: center;
    justify-content: center;
}

.topCenter {
    flex: 6;
}

.topImg {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    object-fit: cover;
    cursor: pointer;
}

.topList {
    display: flex;
    justify-content: center;
    margin: 0;
    padding: 0;
    list-style: none;
}

.topListItem {
    margin-right: 20px;
    font-size: 18px;
    font-weight: 300;
    cursor: pointer;
}

.topSearchIcon {
    font-size: 18px;
    color: #666;
    cursor: pointer;
    margin-left: 15px;
    color: var(--teal);
}

.topLeft a {
    font-size: 24px;
    min-width: 30px;
    color: #666;
    cursor: pointer;
    margin-left: 15px;
    color: var(--teal);
}

